.BankInfo {
    height: 100vh;
    width: 100vh;
    margin-left: 75px;
    align-items: center; 
    justify-items: center; 
    display:grid;
    grid-template-columns: 217px 3fr; 
    grid-column-gap: 200px;
    grid-template-rows: 700px;
    align-items: flex-start;
    
}

.BankInfoTitle {
    grid-column: 2/2;
    grid-row: 1/2;
    width: 750px;
    margin-top: 100px;
    /* padding-left: 50px; */
    text-align: center;
    color: rgb(89, 135, 255);
}